



































import { Component, Vue, Ref } from "vue-property-decorator";
import CallToAction from "../CallToAction.vue";
import FixedHeader from "../../components/header/FixedHeader.vue";
import Api from "../../Api";

@Component({
  components: {
    CallToAction,
    FixedHeader,
  },
})
export default class JoinOurTeam extends Vue {
  showJoinUs = false;
  joinUsBrief='';
  joinUs=[{
      imageUrl:'',
      textHeader:'',
      textContent:'',
      joinLink:'', 
    }]
  mounted() {
      let recaptchaScript = document.createElement('script')
      recaptchaScript.setAttribute('src', '//embed.typeform.com/next/embed.js')
      document.head.appendChild(recaptchaScript)
    }
async created() {
  await this.fetchData();
};

async fetchData() {
   
    await fetch(Api.JoinUs.GetAll)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        let result = data.data;
        this.joinUs=[];
        for(let i=0;i<result.length;i++){
          this.joinUs.push({
            imageUrl: Api.LocalhostImages + result[i].Image,
            textHeader: result[i].Title,
            textContent: result[i].Description,
            joinLink: result[i].Join_Link,
          })
        }
      })
      .catch((error) => {
        console.log(error);
      });

      await fetch(Api.JoinUs.GetJoinUsBrief)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response;
        }
      })
      .then((data) => {
        this.joinUsBrief =data.data.Brief;
      })
      .catch((error) => {
        console.log(error);
      });
  };


}
